import React, {useEffect} from 'react'

let FormToSend = props => {



    let io=(action)=>{
        props.itsRename('TEXT',action.target.value)
    }

    document.addEventListener('keypress', e=>{
        console.log(e);
        e.ctrlKey && e.code==='Enter' && props.itsRename('SEND')
    });
    console.log(document)

    return (<form>
                   <textarea onChange={io} >{props.text}</textarea>
                   <input id='send-mess' type={'reset'} onClick={()=>props.itsRename('SEND')} value={'send'} />
            </form>
    )

}

export default FormToSend;



